<template>
  <div class="dis-flex size flex-column">
    <filter-nav-control
      :filter-nav-list="navList"
      v-model="activeNav"
      @change="charitableTypeChange"
    />

    <div class="flex-1" style="overflow-y: auto;padding: 0 1.071rem 1.429rem;">
      <!--公益项目 Item-->
      <charitable-projects-item :charitable-list="helpList" />
    </div>
  </div>
</template>

<script>
import { helpList } from "../../api/api";
import FilterNavControl from "../../components/common/filter-nav-control";
import CharitableProjectsItem from "../../components/loving-help/charitable-projects-item";
export default {
  name: "charitable-projects",
  components: { CharitableProjectsItem, FilterNavControl },
  data() {
    return {
      navList: [
        { title: "贫困帮扶", key: "poor" },
        { title: "疾病帮扶", key: "disease" }
      ],
      activeNav: "poor",
      helpList: []
    };
  },
  methods: {
    /**
     * @description: 公益类型改变
     * @date 2020/2/12
     */
    charitableTypeChange(event) {
      if (this.activeNav !== event) this.activeNav = event;
      console.log(event);
      if (event == "poor") {
        let data = {
          helpful_type: 1
        };
        helpList(data).then(res => {
          console.log(res);
          this.helpList = res.data.data.data;
        });
      } else {
        let data = {
          helpful_type: 2
        };
        helpList(data).then(res => {
          console.log(res.data.data.data);
          this.helpList = res.data.data.data;
        });
      }
    }
  },
  created() {
    let data = {
      helpful_type: 1
    };
    helpList(data).then(res => {
      console.log(res);
      this.helpList = res.data.data.data;
    });
  }
};
</script>

