<template>
  <div class="dis-flex nav-filter-bt-box justify-space-between">
    <van-button
      v-for="item of filterNavList"
      :key="item.key"
      @click="filterNavChange(item)"
      :class="{active: value === item.key}">{{item.title}}
    </van-button>
  </div>
</template>

<script>
export default {
  name: 'filter-nav-control',
  props: {
    value: {},
    // 筛选导航列表
    filterNavList: {
      type: Array,
      required: true
    }
  },
  methods: {
    /**
       * @description: 筛选导航改变
       * @date 2020/2/12
       */
    filterNavChange (event) {    
      this.$emit('input', event.key)
      this.$emit('change', event.key, event)
    }
  }
}
</script>

<style scoped lang="less">
  /*顶部筛选栏容器*/
  .nav-filter-bt-box {
    padding: 1.071rem;

    button {
      width: 12.143rem;
      height: 2.857rem;
      color: white;
      font-size: 1.071rem;
      background: #E3E3E3;
      border-radius: 0.357rem;
      transition: all 200ms ease-in-out;

      &.active {
        background-color: #FEA503;
      }
    }
  }
</style>
